* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  --white: #fff;
  --blue: #00acdc;
  --navy-blue: #060b35;
}

.card {
  padding-top: 32px;
  display: flex;
  flex-direction: column;
  width: 200px;
  background: var(--white);
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}

.card__img {
  margin-left: 19px;
  margin-bottom: 40px;
  width: 160px;
  height: 134px;
}

.card__title {
  margin: 0 16px 4px;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: var(--navy-blue);
}

.card__product-code {
  margin-left: 16px;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
}

.card__reviews {
  display: flex;
  justify-content: space-between;
  margin: 0 16px 24px;
}

.card__reviews-text {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: var(--navy-blue);
}

.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background-image: url(./images/star.svg);
  background-repeat: no-repeat;
  background-position: 50%;
}

.stars--1 .stars__star:nth-child(-n+1),
.stars--2 .stars__star:nth-child(-n+2),
.stars--3 .stars__star:nth-child(-n+3),
.stars--4 .stars__star:nth-child(-n+4),
.stars--5 .stars__star:nth-child(-n+5) {
  background-image: url(./images/star-active.svg);
}

.card__price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 16px 16px;
}

.card__price-text {
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.card__price-price {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: var(--navy-blue);
}

.card__link {
  margin: 0 16px 16px;
  padding: 12px 70px;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--white);
  background: var(--blue);
  border-radius: 5px;
}

.card:hover .card__link {
  background: var(--white);
  border: 1px solid var(--blue);
  border-radius: 5px;
  color: var(--blue);
}
